<script>
import notice from './../components/notice'
export default {
    data () {
      return {
        couponBox:false,
        date:[{
          value:0,
          name:'7'
        },{
          value:1,
          name:'30'
        },{
          value:2,
          name:'90'
        }],
        mydate:0,
        comments:[{
          value:0,
          name:''
        },{
          value:1,
          name:'(1-2)'
        },{
          value:2,
          name:'(3)'
        },{
          value:3,
          name:'(4-5)'
        }],
        mycomment:0,
        dateStyle:'border:1px solid #e6e6e6',
        pickerOptions2: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        commentList:[{
          name:'',
          checked:false
        },{
          name:'2 1',
          checked:false
        },{
          name:'5020',
          checked:false
        },{
          name:'2',
          checked:false
        }],
        commentSearchTime:''
      }
    },
    methods:{
      //
      timeCommentSearch(){
        this.$stamp(null,this.commentSearchTime)
      },
      //
      chooseDate(val){
        this.$stamp(null,val)
        this.mydate = val
      },
      //
      chooseComment(val){
        this.$stamp(null,val)
        this.mycomment = val
      },
      // 
      sendCoupon(){
        this.couponBox = true
      },
      //
      comfiredSend(){
        this.$stamp(null,this.commentList)
        this.couponBox = false
      }
    },
    computed: {
      
    },
    components: {
      notice
    },
    created(){
      
    }
}
</script>

